import React from 'react'
import {Link,NavLink} from 'react-router-dom'
import Cookie from "js-cookie";
import Pubsub from 'pubsub-js';


class App extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      auth:''
    }
  }
  componentDidMount(){
    var that =this;
    Pubsub.subscribe('auth',function(msg,data){
      that.setState({
        auth:data
      })
    })
  }


  render() {
    if(Cookie.get("auth")){
      return (
        <div>
          <nav className="navbar navbar-light">
            <div className="container">
              <a className="navbar-brand" href="index.html">
                conduit
              </a>
              <ul className="nav navbar-nav pull-xs-right">
                <li className="nav-item">
                  <NavLink className="nav-link" to="/" activeClassName="active" >
                    Home
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="/ariticle" activeClassName="active">
                    <i className="ion-compose"></i>&nbsp;New Article
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="/settings" activeClassName="active">
                    <i className="ion-gear-a"></i>&nbsp;Settings
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link ng-binding active" ui-sref-active="active" ui-sref="app.profile.main({ username: $ctrl.currentUser.username })"  to="/profile" activeClassName="active">
                    <img className="user-pic" ng-src="https://z3.ax1x.com/2021/04/29/gFXnCF.png" src="https://z3.ax1x.com/2021/04/30/gAqOM9.jpg"/>
                    i'm your father
                  </NavLink>
                </li>
              </ul>
            </div>
          </nav>
        </div>
      );
    }else{
      return (
        <div>
          <nav className="navbar navbar-light">
            <div className="container">
              <a className="navbar-brand" href="index.html">
                conduit
              </a>
              <ul className="nav navbar-nav pull-xs-right">
                <li className="nav-item">
                  <NavLink className="nav-link" exact to="/" activeClassName="active" >
                    Home
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="/login" activeClassName="active">
                    Sign in
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="/register" activeClassName="active">
                    Sign up
                  </NavLink>
                </li>
              </ul>
            </div>
          </nav>
        </div>
      );
    }
   
  }
}

export default App;