// import logo from './logo.svg';

import './App.css';
import './main.css'
import React from 'react'
import Header from './Header/header'
import Footer from './Footer/footer'
import Ariticle from './Article/article'
import Login from './Login/login'
import Settings from './Settings/settings'
import Create from './Create/create'
import Profile from './profile/profile'
import { BrowserRouter as Router, Route ,Link, Switch} from "react-router-dom";
import Home from './Home/index';
import Cookie from 'js-cookie'




class App extends React.Component{

  render(){
    if(Cookie.get('auth')){
      return(
        <div>
        <Router>
        <Header />
          <Switch>
            <Route exact path='/' component={Home}></Route>
            <Route  path='/create' component={Create}></Route>
            <Route  path='/ariticle' component={Ariticle}></Route>
           
            <Route  path='/register' component={Home}></Route>
            <Route  path='/login' component={Home}></Route>
            <Route  path='/settings' component={Settings}></Route>
            <Route  path='/profile' component={Profile}></Route>
            
          </Switch>
          <Footer />
        </Router>
        
      </div>
      )
    }else{
      return(
        <div>
        <Router>
        <Header />
          <Switch>
            <Route exact path='/' component={Home}></Route>
            <Route  path='/create' component={Home}></Route>
            <Route  path='/ariticle' component={Home}></Route>
           
            <Route  path='/register' component={Login}></Route>
            <Route  path='/login' component={Login}></Route>
            <Route  path='/settings' component={Home}></Route>
            <Route  path='/profile' component={Home}></Route>
            
          </Switch>
          <Footer />
        </Router>
        
      </div>
      )
    }
  }
}

export default App;
