import obj  from "./api";

//点赞
export let addFavorite = (item) => {
  console.log("=============");
  var slug = item.slug;



  //发送点赞的请求
  return obj({
    method: "POST",
    url: `/articles/${slug}/favorite`,
  });
};

//取消点赞
export let deleFavorite = (item) => {
  //     //发送取消点赞的请求
  var slug = item.slug;
  //发送点赞的请求
  return obj({
    method: "DELETE",
    url: `/articles/${slug}/favorite`,
  });
};
