import React from 'react';
import Cookie from 'js-cookie'
import Pubsub from 'pubsub-js'
import obj from '../api'
export default



 class Login extends React.Component{
    constructor(props) {
        super(props)
        this.state = {
          email:'',
          password:'',
          username:''
        }
    }
    componentDidMount() {
        // console.log("-----", this.props);
        // obj({
        //         method: "POST",
        //         url: "/users/login",
        //         data: {
        //           "user": {
        //             "email": "2713677270@qq.com",
        //             "password": "123123123"
        //           }
        //         }
        //       }).then(res => {
        //         console.log(res);
                
        //       })
        Pubsub.subscribe('value',(msg,data)=>{
          this.setState({
            username:data
          })
        })
      }
    isLogin(){
        return this.props.match.path === '/login';
    }
    email(e){
      this.setState({
        email:e.target.value
      })
    }
    password(e){
      this.setState({
        password:e.target.value
      })
    }
    sign(e){
      e.preventDefault();
      let user ={
        username:this.state.username?this.state.username:'',
        email :this.state.email,
        password :this.state.password
      }
      obj({
              method: "POST",
              url: this.state.username?'/users':'/users/login',
              data: {
                user
                }
            }).then(res => {
              console.log(res);
                Cookie.set('auth',res.data.user.token)
                this.props.history.push('/');
                Pubsub.publish('auth',res.data.user.token)
            })
    }





    render() {
        return <div>
            <div className="auth-page">
  <div className="container page">
    <div className="row">

      <div className="col-md-6 offset-md-3 col-xs-12">
        <h1 className="text-xs-center">
            {this.isLogin() ? 'Sign in' :'Sign up'}
        </h1>
        <p className="text-xs-center">
          <a href="">
              {this.isLogin() ? 'Need an account' :'Have an account'}
          </a>
        </p>

        <ul className="error-messages">
          <li>That email is already taken</li>
        </ul>

        <form onSubmit={this.sign.bind(this)}>
            {this.isLogin() ? '': <SignUp/>}
          {/* <fieldset className="form-group">
            <input className="form-control form-control-lg" type="text" placeholder="Your Name" />
          </fieldset> */}
          <fieldset className="form-group">
            <input className="form-control form-control-lg" type="text" placeholder="Email" onBlur={this.email.bind(this)} />
          </fieldset>
          <fieldset className="form-group">
            <input className="form-control form-control-lg" type="password" placeholder="Password"  onBlur={this.password.bind(this)} />
          </fieldset>
          <button className="btn btn-lg btn-primary pull-xs-right">
            {this.isLogin() ? 'Sign in' : 'Sign up'}
          </button>
        </form>
      </div>

    </div>
  </div>
</div>
        </div>
    }
}
class SignUp extends React.Component{
  constructor(prors){
    super(prors)
  }
  blur(e){
    var value = e.target.value
    Pubsub.publish('value',value)
  }
    render(){
        return(
            <div>
                <fieldset className="form-group">
          <input
            className="form-control form-control-lg"
            type="text"
            placeholder="Your Name"
            onBlur={this.blur.bind(this)}
          />
        </fieldset>
            </div>
        )
    }
}